.App {
  text-align: center;
  min-height: 100%;
}

html{
  background-color: white;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

select {
	background: url(data:image/svg+xml;base64,) no-repeat 95% 50%;
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	height: 30px;
	width: 100px;
	padding: 5px;
}
.big { font-family: Merriweather; font-size: 56px; font-style: normal; font-variant: normal; font-weight: 800; line-height: 56px; } 
h1 { font-family: Merriweather; font-size: 40px; font-style: normal; font-variant: normal; font-weight: 700; line-height: 40px; } 
h2 { font-family: Merriweather; font-size: 28px; font-style: normal; font-variant: normal; font-weight: 600; line-height: 28px; } 
h3 { font-family: Merriweather; font-size: 20px; font-style: normal; font-variant: normal; font-weight: 500; line-height: 20px; } 
h4 { font-family: Merriweather; font-size: 18px; font-style: normal; font-variant: normal; font-weight: 400; line-height: 18px; } 
h5 { font-family: Merriweather; font-size: 16px; font-style: normal; font-variant: normal; font-weight: 300; line-height: 16px; } 

p { font-family: Merriweather; font-size: 14px; font-style: normal; font-variant: normal; font-weight: 400; line-height: 20px; } 
a { color: black; font-family: Merriweather; font-size: 30px; font-style: normal; font-variant: normal; font-weight: 400; line-height: 20px; } 
footer { font-family: Merriweather; font-size: 14px; font-style: normal; font-variant: normal; font-weight: 400; line-height: 20px; }
blockquote { font-family: Merriweather; font-size: 21px; font-style: normal; font-variant: normal; font-weight: 400; line-height: 30px; } 
pre { font-family: Merriweather; font-size: 13px; font-style: normal; font-variant: normal; font-weight: 400; line-height: 18.5714px; }

